import React from 'react';
import PropTypes from 'prop-types';
import AvatarSelector from './AvatarSelector';
import ProfileForm from './ProfileForm';
import './ProfileHeader.css';

const ProfileHeader = ({ 
  profile, 
  currentUser, 
  editing, 
  formData, 
  avatarOptions, 
  showAvatarSelector,
  onEdit,
  onAvatarSelect,
  onAvatarSelectorToggle,
  onSubmit,
  onChange,
  onCancel
}) => (
  <div className="profile-header">
    <div className="avatar-container">
      <img 
        src={profile?.avatarUrl || avatarOptions[0].url} 
        alt="Profile" 
        className="profile-avatar"
      />
      {editing && (
        <button 
          className="change-avatar-button"
          onClick={onAvatarSelectorToggle}
        >
          Change Avatar
        </button>
      )}
      {showAvatarSelector && (
        <AvatarSelector
          avatarOptions={avatarOptions}
          selectedAvatar={formData.avatarUrl}
          onSelect={onAvatarSelect}
          onClose={() => onAvatarSelectorToggle(false)}
        />
      )}
    </div>
    <div className="profile-info">
      <div className="profile-main">
        {!editing ? (
          <>
            <h1 className="nickname">{profile?.nickname || 'Set your nickname'}</h1>
            <p className="email">{currentUser.email}</p>
            <p className="country">{profile?.country || 'Set your country'}</p>
            <button onClick={onEdit} className="edit-button">
              Edit Profile
            </button>
          </>
        ) : (
          <ProfileForm
            formData={formData}
            onSubmit={onSubmit}
            onChange={onChange}
            onCancel={onCancel}
          />
        )}
      </div>
    </div>
  </div>
);

ProfileHeader.propTypes = {
  profile: PropTypes.shape({
    nickname: PropTypes.string,
    country: PropTypes.string,
    avatarUrl: PropTypes.string
  }),
  currentUser: PropTypes.shape({
    email: PropTypes.string.isRequired
  }).isRequired,
  editing: PropTypes.bool.isRequired,
  formData: PropTypes.shape({
    nickname: PropTypes.string.isRequired,
    country: PropTypes.string.isRequired,
    avatarUrl: PropTypes.string.isRequired
  }).isRequired,
  avatarOptions: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    url: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired
  })).isRequired,
  showAvatarSelector: PropTypes.bool.isRequired,
  onEdit: PropTypes.func.isRequired,
  onAvatarSelect: PropTypes.func.isRequired,
  onAvatarSelectorToggle: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
};

export default ProfileHeader; 